* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: aliceblue;
  height: 100%;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-check {
  padding-top: 10px;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 5px 5px;
}

.form-group {
  padding-top: 20px;
  -webkit-align-items: left;
          align-items: left;
}

.col-form-label {
  text-align: left;
  display: block;
  padding-left: 10px;
}

.form-control select {
  width: 70%;
  height: 30px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
}

.form-control textarea {
  display: inline-block;
  width: 70%;
  height: 200px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
}

.form-control-check {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.form-control-check label {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.form-control-check input {
  -webkit-flex: 2 1;
          flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

