.toast {
  width: auto;
  height: auto;
  position: fixed;
  padding: 3px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.toast-body {
  background-color: ghostwhite;
  font-size: larger;
}

#messageToastModal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.encounter {
  margin: 0;
  padding: 0;
  /* box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.16),
    1px 1px 5px 1px rgba(0, 0, 0, 0.26);
    */
  border-color: lightgray;
  border-style: solid;
  border-width: 2px;
  background-color: ghostwhite;
}

.referrals {
  padding-top: 0px;
  height: 100vh;
}

.newReferral {
  /* box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.16),
    1px 2px 5px 1px rgba(0, 0, 0, 0.26); 
    */
  /* box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.16),
    1px 1px 5px 1px rgba(0, 0, 0, 0.26);
    */
  border: 1px, solid, lightgray;
  border-color: lightgray;
  border-style: solid;
  border-width: thin;
  border-radius: 5px;
  padding: 30px;
  margin: 20px;
  background-color: ghostwhite;
}

.encounterSummary {
  /* box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.16),
    1px 1px 5px 1px rgba(0, 0, 0, 0.26);
    */
  border-color: lightgray;
  border-style: solid;
  border-width: 2px;
  border-left-style: none;
  border-right-style: none;
  padding: 10px;
  padding-right: 30px;
  margin: 0px;
  margin-top: 0px;
  background-color: white;
}

.referralList {
  /* box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.16),
    1px 1px 5px 1px rgba(0, 0, 0, 0.26);
    */
  border-color: lightgray;
  border-style: solid;
  border-width: thin;
  border-radius: 5px;
  padding: 0px;
  padding-top: 0;
  padding-bottom: 0;
  margin: 20px;
  background-color: ghostwhite;
}

.list-hover {
  cursor: pointer;
  margin-bottom: 0px;
  margin-top: 0px;
  border-top-color: lightgray;
  border-top-style: dashed;
  border-bottom-color: lightgray;
  border-bottom-style: dashed;
  border-width: thin;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
}

.list-hover.selected {
  background-color: white;
  border-top-color: lightgray;
  border-top-style: solid;
  border-bottom-color: lightgray;
  border-bottom-style: solid;
  border-width: thin;
}

.table-hover {
  margin-bottom: 0px;
  margin-top: 0px;
  border-top-color: lightgray;
  border-top-style: solid;
  border-bottom-color: lightgray;
  border-bottom-style: solid;
  border-width: thin;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
  vertical-align: middle;
}

.table-hover tbody tr.table-ghostwhite {
  background-color: ghostwhite;
}

.table-header {
  font-size: 15px;
  font: bold;
}

.hidden-row {
  cursor: pointer;
  margin-bottom: 0px;
  margin-top: 0px;
  background-color: ghostwhite;
  padding-top: 18px;
  padding-bottom: 0px;
}

.hidden-row:hover {
  background-color: #ededed;
}

.help-content {
  padding-left: 15px;
  padding-right: 15px;
  background-color: ghostwhite;
}

.table-header-cell {
  margin-top: 2px;
  margin-bottom: 2px;
  padding: 2px;
}

.table-header .btn:hover {
  cursor: pointer;
}

.table-header .btn {
  text-align: center;
  font-size: 15px;
  font: bold;
}

#referralStatus .table {
  text-align: center;
  margin: auto;
  vertical-align: middle;
}

/* For Mobile */
@media screen and (min-width: 7800px) {
  .encounter {
    height: 100vh;
  }
}

/* For Tablets */
@media screen and (max-width: 780px) {
  .encounterSummary {
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 20px;
  }

  .referralList {
    margin-left: 0px;
    margin-right: 0px;
  }

  .newReferral {
    margin-left: 0px;
    margin-right: 0px;
  }
}

.App {
  text-align: center;
  background-color: aliceblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

